<template>
  <div class="mobile-container">
    <Header/>

    <div class="m-content">
        <div class="m-pos-bg">
          <img src="@/assets/m/bg.png" alt="" class="bg-img">
          <div class="m-pos-con">
            <div class="pos-con-img"><img src="@/assets/img/exchange2.png" alt="">{{ $t('lang.block_title_block') }} {{ blockData.height }} </div>
            <div class="m-nodeid">{{ blockData.hash }}</div>
          </div>
        </div>
        <div class="m-btn-wrap">
          <div class="prev-btn m-btn" @click="handelReload(previous)">&lt;&lt;{{ $t('lang.block_page_pre') }}</div>
          <div class="next-btn m-btn" @click="handelReload(next)">{{ $t('lang.block_page_next') }}&gt;&gt;</div>
        </div>
        <div class="m-con">
          <div class="m-head">
            <div class="m-item">
                <div class="item-lebel">{{ filterDate(blockData.timestamp) }}</div>
                <p>{{ $t('lang.block_detail1') }}</p>
            </div>
            <div class="m-item">
                <div class="item-lebel"> {{ blockData.value_out }} GKC</div>
                <p>{{ $t('lang.block_detail2') }}</p>
            </div>
            <div class="m-item">
                <div class="item-lebel"> {{ blockData.fees }} GKC</div>
                <p>{{ $t('lang.block_detail3') }}</p>
            </div>
          </div>
          <div class="head-btn">
            <div class="to-details" @click="handleHidden">{{ $t('lang.block_detail_button') }}</div>
          </div>
          <div class="block-con" v-show="blockinfo">
            <div class="block-item">
              <label for="">{{ $t('lang.block_detail4') }}：</label>
              <p>{{ blockData.size }}</p>
            </div>
            <div class="block-item">
              <label for="">{{ $t('lang.block_detail5') }}：</label>
              <p>{{ blockData.tx_num }}</p>
            </div>
            <div class="block-item">
              <label for="">{{ $t('lang.block_detail6') }}：</label>
              <p>{{ blockData.generated }}</p>
            </div>
            <div class="block-item">
              <label for="">{{ $t('lang.block_detail7') }}：</label>
              <p>{{ blockData.difficulty }}</p>
            </div>
            <div class="block-item">
              <label for="">{{ $t('lang.block_detail8') }}：</label>
              <p>{{ blockData.bits }}</p>
            </div>
            <div class="block-item">
              <label for="">{{ $t('lang.block_detail9') }}：</label>
              <p>{{ blockData.nonce }}</p>
            </div>
            <div class="block-item">
              <label for="">{{ $t('lang.block_detail10') }}：</label>
              <p>{{ blockData.version }}</p>
            </div>
            <div class="block-item">
              <label for="">{{ $t('lang.block_detail11') }}：</label>
              <p>{{ blockData.merkle_root }}</p>
            </div>
          </div>
        </div>
        <div class="m-tips">{{ $t('lang.block_trans_title') }}</div>
        <div class="m-con m-bg"  v-if="transData.length > 0">
          <div class="m-con-log"  v-for="(item, index) in transData" :key="index" :prop="item.prop">
            <div class="m-entrust" v-if="item.type === 'Entrust'"><img src="@/assets/img/pos.png" alt="">Pos Entrust</div>
            <div v-if="item.type === 'Deprive'" class="m-entrust"><img src="@/assets/img/pos.png" alt="">PoS Deprive</div>
            <div v-if="item.type === 'CreateAgent'" class="m-entrust"><img src="@/assets/img/pos.png" alt="">PoS Create Agent</div>
            <div v-if="item.type === 'ResignAgent'" class="m-entrust"><img src="@/assets/img/pos.png" alt="">PoS Resign Agent</div>
            <div v-if="item.type === 'Create'" class="m-entrust"><img src="@/assets/img/contract.png" alt="">Contract</div>
            <div v-if="item.type === 'Call'" class="m-entrust"><img src="@/assets/img/contract.png" alt="">Contract</div>
            <div class="log-title">
              <img src="@/assets/img/exchange.png" alt="">
              <div class="log-link"><router-link tag="a" :to="{path:'/mtx',query:{txid: item.txid}}">{{ item.txid }}</router-link></div>
            </div>
            <div class="log-con">
              <div class="log-con-title">{{ $t('lang.block_trans1') }}</div>
              <div class="log-con-info">
                <div class="info-item" v-for="(item2, index2) in item.from_array" :key="index2">
                  <div class="item-child" v-for="(item3, index3) in item2" :key="index3">
                    <div class="item-form"><span>{{ item3 }} GKC</span> from</div>
                    <div class="item-details">
                      <img src="@/assets/img/qr.png" alt="">
                      <router-link v-if=isGoto(index3) tag="a" :to="{path:'/maddress',query:{address: index3}}">{{ index3 }}</router-link>
                      <a v-else href="javascript:void(0)">{{ index3 }}</a>
<!--                      <router-link tag="a" :to="{path:'/maddress',query:{address: index3}}">{{ index3 }}</router-link>-->
                    </div>
                  </div>
                </div>
                <div class="log-con-img"><img src="@/assets/m/down.png" alt=""></div>
                <div class="info-item" v-for="(item4, index4) in item.to_array" :key="index4">
                  <div class="item-child" v-for="(item5, index5) in item4" :key="index5">
                    <div class="item-form" v-if="index5 === 'unknown'"><span>Empty</span></div>
                    <div class="item-form" v-else-if="index5 === 'Non-standard script'"><span>Cointract</span></div>
                    <div class="item-form" v-else><span>{{ item5 }} GKC</span> To</div>
                    <div class="item-details" v-if="index5 === 'unknown'">
                        <img src="@/assets/img/unkown.png" alt="">
                        <p v-if="index === 0" style="display: inline-block">Coinbase</p>
                        <p v-else style="display: inline-block">Coinstake</p>
                    </div>
                    <div class="item-details" v-else-if="index5 === 'Non-standard script'">
                      <img src="@/assets/img/unkown.png" alt="">
                      <p style="display: inline-block">{{ index5 }}</p>
                    </div>
                    <div class="item-details" v-else>
                      <img src="@/assets/img/qr.png" alt="">
                      <router-link v-if=isGoto(index5) tag="a" :to="{path:'/maddress',query:{address: index5}}">{{ index5 }}</router-link>
                      <a v-else href="javascript:void(0)">{{ index5 }}</a>
<!--                      <router-link tag="a" :to="{path:'/maddress',query:{address: index5}}">{{ index5 }}</router-link>-->
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="log-total" v-if="item.total_out !== ''">
              {{ $t('lang.block_trans3') }} ：<span>{{ item.total_out }} GKC</span>
            </div>
          </div>
        </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import Header from '@/components/MHeader'
import Footer from '@/components/MFooter'
import pageSize from '@/components/MPage'
import dateFns from 'date-fns'
import QRCode from 'qrcodejs2'
export default {
    components: {
        Header,
        Footer,
        pageSize
    },
    data() {
            return {
                transData: [],
                blockData: [],
                height: '',
                hash: '',
                previous: '',
                next: '',
                blockinfo: false,
                maxHeight: ''

            }
        },
        created() {
            this.height = this.$route.query.height;
            this.hash = this.$route.query.hash;
        },
        watch: {
            $route(){
                this.height = this.$route.query.height;
                this.hash = this.$route.query.hash;
                if (!this.hash) {
                    this.getBlockInfoByHeight(this.height)
                } else {
                    this.getBlockInfoByHash(this.hash)
                }
            }
        },
        computed: {},
        mounted() {
            this.getMaxHeight()
            if (this.previous === '') {
                this.previous = 0
            }
            if (this.next === '') {
                this.next = this.maxHeight
            }
            if (this.maxHeight === '') {
                this.maxHeight = 0
            }
            if (!this.hash) {
                this.getBlockInfoByHeight(this.height)
            } else {
                this.getBlockInfoByHash(this.hash)
            }

        },
        methods: {
            getBlockInfoByHeight(height) {
                height = parseInt(height)
                this.getMaxHeight()
                this.$http.post("api/block/info", {height: height}).then(res => {
                    if (res.data.code === 200) {
                        this.blockData = res.data.data.block
                        this.previous = this.max(parseInt(res.data.data.block.height) - 1, 0)
                        this.next = this.min(parseInt(res.data.data.block.height) + 1, parseInt(this.maxHeight))
                        this.transData = res.data.data.list

                        setTimeout(() =>{
                            $(".qrdata").html("")
                            // for (const index in this.transData ) {
                            //     for (const index2 in this.transData[index].to_array) {
                            //         for (const index3 in this.transData[index].to_array[index2] ) {
                            //             this.qrcode(20, 20, index3 + index2 + index, index3)
                            //         }
                            //     }
                            //     for (const index4 in this.transData[index].from_array) {
                            //         for (const index5 in this.transData[index].from_array[index4] ) {
                            //             this.qrcode(20, 20, index5 + index4 + index, index5)
                            //         }
                            //     }
                            // }
                        },500);

                    }else{
                        this.previous = 0
                        this.next = this.maxHeight
                    }
                })
            },
            getBlockInfoByHash(hash) {
                this.$http.post("api/block/hash", {hash: hash}).then(res => {
                    if (res.data.code === 200) {
                        this.blockData = res.data.data.block
                        this.previous = this.max(parseInt(res.data.data.block.height) - 1, 0)
                        this.next = this.min(parseInt(res.data.data.block.height) + 1, parseInt(this.maxHeight))
                        this.transData = res.data.data.list
                    }
                })
            },
            getMaxHeight() {
                this.$http.get("api/block/height").then(res => {
                    if (res.data.code === 200) {
                        this.maxHeight = res.data.data.height
                    }
                })
            },
            handelReload(height) {
                this.$router.push({path: '/mblock', query: {height: height}});
                this.height = height;
                this.getBlockInfoByHeight(this.height);
            },
            max(first, second) {
                return first > second ? first : second;
            },
            min(first, second) {
                return first < second ? first : second;
            },
            handleHidden() {
                if (!this.blockinfo) {
                    this.blockinfo = true
                } else {
                    this.blockinfo = false
                }
            },
            qrcode(width, height, id, address) {
                new QRCode(id, {
                    text: address,
                    width: width,
                    height: height,
                    colorDark: '#333333',
                    colorLight: '#ffffff',
                    correctLevel: QRCode.CorrectLevel.L
                })
            },
            filterDate(value, type) {
                if (!value) return ''
                if(value > 0){
                    value = value * 1000
                }
                const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
                return dateFns.format(value, filterType)
            },
            isGoto(str) {
              if (str.indexOf("G") === 0 && str.length === 34){
                return true;
              }else{
                return false;
              }
            }
        }
}


</script>


<style scoped>
  .m-pos-bg{
    text-align: left;
    position: relative;
    margin-bottom: .8rem;
  }
  .m-pos-bg .bg-img{
    width: 100%;
    height: 8rem;
  }
  .m-pos-con{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    color: #ffffff;
  }
  .pos-con-img{
    margin: 1rem;
    display: flex;
    align-items: center;
  }
  .pos-con-img img{
    width: 2rem;
    height: 2rem;
    margin-right: .8rem;
  }
  .m-nodeid{
    word-wrap:break-word;
    margin: 0 5rem 0 3rem;
  }
  .m-btn-wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
  }
  .m-btn{
    width: 100px;
    font-size: 12px;
    text-align: center;
    background: #F0F0F0;
    line-height: 32px;
    border-radius: 4px;
  }
    .m-content{
        padding: 1rem;
        background: #E6E9EE;
    }
    .m-head{
        background: #ffffff;
    }
    .m-head .m-item{
        text-align: center;
        border-bottom: 1px solid #F0F0F0;
        padding: 1rem 0;
    }
    .m-item p{
        color: #888888;
        font-size: 12px;
    }
    .item-lebel{
        font-size: 17px;
        margin-bottom: .5rem;
    }
    .m-tips{
        padding: .7rem;
        text-align: left;
    }

    .m-con{
      background: #ffffff;
    }
    .head-btn{
      text-align: center;
      padding: 1.5rem 0;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .head-btn .to-details{
      width:10rem;
      line-height:3rem;
      background:rgba(0,93,166,1);
      opacity:1;
      border-radius:4px;
      color: #ffffff;
    }
</style>



